* {
    box-sizing: border-box;
}

#hello {
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid white; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .0001em; /* Adjust as needed */
    animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
}
  
/* The typing effect */
@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}
  
/* The typewriter cursor effect */
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: white; }
}





/* ----------FONTS---------- */
@font-face {
    font-family: Roboto;
    src: local("Roboto-Bold"),
    url("../asset/fonts/Roboto-Bold.ttf") format("truetype");
}

@font-face {
    font-family: Futura;
    src: local("Futura-Medium-bt"),
    url("../asset/fonts/Futura-Medium-bt.ttf") format("truetype");
}





/* ----------CONTENT: HOME/INDEX---------- */
#main-div{
    background-color: rgb(145, 167, 241);;
}

.row::after {
    content: "";
    clear: both;
    display: table;
}
  
[class*="col-"] {
    float: left;
    padding: 15px;
}

/* For mobile phones: */
[class*="col-"] {
    width: 100%;
}

.profilepic {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    height: 280px;
}

#desc {
    margin-top: 0px;
    text-align: center;
}

#hello {
    display: block;
    padding-block: 5px;
    padding-left: 10px;
    font-size: 60px;
    font-family: Roboto;
    background-color: white;
    margin-bottom: 20px;
}
  
.intro {
    font-family: Futura;
    font-size: 20px;
    margin-bottom: 20px;
}

.btn-home {
    height: 50px;
    width: 150px;
    font-size: 20px;
    color: black;
    background-color: white;
    font-family: Futura;
    border-width: 3px;
    border-radius:10px;
    border-color: black;
}
  
.link-btn {
    color: black;
}

.bubble {
    display: block;
    margin-right: 0;
    position: relative;
    background: white;
    color: black;
    font-family: Futura;
    font-size: 15px;
    line-height: 30px;
    text-align: center;
    max-width: 170px;
    max-height: fit-content;
    border-radius: 10px;
    padding: 5px;
}
.bubble:after {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    z-index: 1;
    border-style: solid;
    border-width: 0 30px 30px 0;
    border-color: transparent white transparent transparent;
    bottom: -30px;
    left: 75%;
    margin-left: -15px;
}





/* ----------MEDIA ADJUST---------- */
@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}

    .profilepic {min-height: 380px;}
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}
  
    .profilepic {min-height: 450px;}
    
    #desc {
      margin-top: 20px;
      text-align: left;
    }

    .intro {font-size: 30px;}
}
  
@media only screen and (min-width: 980px) {
    .profilepic {
        margin-left: 0;
        min-height: 570px;
    }
  
    #desc {
        margin-top: 75px;
        text-align: left;
    }
} 