* {
    box-sizing: border-box;
}

/* ----------FONTS---------- */
@font-face {
    font-family: Roboto;
    src: local("Roboto-Bold"),
    url("../asset/fonts/Roboto-Bold.ttf") format("truetype");
}

@font-face {
    font-family: Futura;
    src: local("Futura-Medium-bt"),
    url("../asset/fonts/Futura-Medium-bt.ttf") format("truetype");
}





/* ----------CONTENt: ABOUT---------- */
.main-div {
    background-color: white;
}

/* .row{
    display: flex;
    width: 100%;
} */

.row::after {
    content: "";
    clear: both;
    display: table;
}

/* .card-body {
    flex: 1;
} */
  
[class*="col-"] {
    float: left;
    padding: 15px;
}
  
/* For mobile phones: */
[class*="col-"] {
    width: 100%;
}

#project {
    font-size: xx-large;
    font-family: Roboto;
    text-align: center;
}

.project-tittle {
    font-family: Roboto;
    font-size: large;
}

.project-desc {
    font-family: Futura;
    font-size: large;
    height: 100px;
    overflow: auto;
}

.price {
    font-family: Roboto;
}

.buy {
    font-family: Futura;
}





/* ----------MEDIA ADJUST---------- */
@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}

    /* .img-project{
        max-height: 200px;
    } */

    .project-tittle {
        font-size: medium;
    }

    .project-desc {
        font-size: medium;
    }
}

@media only screen and (min-width: 980px) {
    #desc-about-me {font-size: large;}

    .project-tittle {
        font-size: large;
    }

    .project-desc {
        font-size: medium;
        height: 75px;
    }
}